* {
  box-sizing: border-box;
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 8px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  width: 20%;
  border: 1px solid #212121;
  border-radius: 2px 2px 2px 0;
  outline: none;
  margin-bottom: 0;
  padding: 8px;
}

#search-box:focus {
  border: 2px solid #3cb371;
}

.country-list {
  max-width: 20%;
  border: 2px solid #3cb371;
  border-top: none;
  border-radius: 0 0 10px 10px;
  padding: 8px;
  list-style: none;
  transition: transform .25s;
  overflow: hidden;
  transform: translateY(-100%);
}

.overlay {
  overflow: hidden;
}

.flag {
  width: 24px;
  height: 24px;
  background-size: cover;
  background-repeat: none;
}

.country__item {
  align-items: center;
  gap: 20px;
  margin-bottom: 8px;
  display: flex;
}

.modal p {
  margin-right: 8px;
  font-size: 18px;
  font-weight: 700;
}

.modal span {
  margin-left: 15px;
  font-weight: 400;
  display: inline-block;
}

span.country--Big {
  font-size: 32px;
  font-weight: 700;
}

span.flag--Big {
  width: 40px;
  height: 40px;
  background-size: cover;
  background-repeat: none;
  margin: 0;
  display: inline;
}

.title {
  align-content: center;
  display: flex;
}

.modal {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.modal-window {
  opacity: 0;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
}

.hidden {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
}

